import React from 'react'
import Link from 'gatsby-link'
import styled from 'styled-components'

class PostListing extends React.Component {
  getPostList() {
    const postList = []
    this.props.postEdges.forEach(postEdge => {
      postList.push({
        path: postEdge.node.fields.slug,
        tags: postEdge.node.frontmatter.tags,
        cover: postEdge.node.frontmatter.cover,
        title: postEdge.node.frontmatter.title,
        date: postEdge.node.frontmatter.date,
        excerpt: postEdge.node.excerpt,
        timeToRead: postEdge.node.timeToRead
      })
    })
    return postList
  }
  render() {
    const postList = this.getPostList()
    return (
      <PostListingContainer>
        {/* Your post list here. */
        postList.map(post => (
          <FlexOneThirdCol>
            <Link to={post.path} key={post.title}>
              <div style={{fontSize: 30, lineHeight: '1.2em', minHeight: 80, fontWeight: 200}}>{post.title}</div>
              <img src={post.cover} alt="" />
            </Link>
            <p className="post-date">{post.date}</p>
            <p className="post-excerpt">{post.excerpt}</p>
            <p className="post-time-to-read">Est. Reading: {post.timeToRead} mins.</p>
          </FlexOneThirdCol>
        ))}
      </PostListingContainer>
    )
  }
}

const PostListingContainer = styled.div`
  display: flex;
  flex-direction: row;
  text-align: center;
  flex-wrap: wrap;
  p {
    margin-bottom: 10px;
    text-align: justify;
  }

  @media screen and (max-width:600px) {
    flex-wrap: wrap;
  }
`

const FlexOneThirdCol = styled.div`
  display: flex;
  flex-direction: column;
  flex: 0 1 33%;
  padding-left: 40px;

  img {
    max-width: 100%;
  }
  p {
    margin-top: 0;
    text-align: justify;
  }
  ul {
    margin-top: -15px;
  }

  @media screen and (max-width:600px) {
    flex: 0 1 100%;
    margin-left: 0;
  }
`

export default PostListing



